@charset "utf-8";

/**
 * 底部操作区
 */
.ui-btn-group,.ui-btn-group-tiled{
    display: -webkit-box;
    width: 100%;
    box-sizing: border-box;
}
.ui-btn-group button,.ui-btn-group-tiled button{
    display: block;
    -webkit-box-flex: 1;
}
.ui-btn-group-tiled .ui-btn:first-child{
    margin-right: 15px;
}
.ui-btn-group button{
    color: #00a5e0;
    line-height: 50px;
    height: 48px;
    text-align: center;
    font-size: 16px;
    border-right: 1px #c8c7cc solid;
    width: 100%;
    border-top: 1px solid #c8c7cc;
    &:active {
        background-color: rgba(0,0,0,.1);
    }
    &:last-child{
        border-right: none;
    } 
}
.ui-btn-group-bottom {
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 10;
    background-color: #fff;
    button {
        color: #00a5e0;
        background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #f9f9f9), to(#e0e0e0));

        &:active {
            background-color: none;
            color: rgba(0,165,224,.4);
        }
    }
}

@media screen and (-webkit-min-device-pixel-ratio:2.0){
    .ui-btn-group button{
        position: relative;
        border: 0;
    }
    .ui-btn-group button:before{
        content: "";
        position: absolute;
        -webkit-transform-origin: right top;
        width: 0;
        border-right: 1px solid #c8c7cc;
        right: 0;
        top:0;
        bottom: 0;
        -webkit-transform: scaleX(.5);
    }
    .ui-btn-group button:after{
        content: "";
        position: absolute;
        -webkit-transform-origin: right top;
        left: 0;
        right: 0;
        height: 0;
        border-top: 1px solid #c8c7cc;
        top: 0;
        -webkit-transform: scaleY(.5);
    }
    .ui-btn-group button:last-child:before{
        border-right: none;
    }
}